<template>
    <div class="top-root">
        <div class="top-con">
        <el-button-group>
            <el-autocomplete
                popper-class="marker-autocomplete"
                custom-item="marker-autocomplete"
                v-model="searchVal"
                :fetch-suggestions="querySearchAsync"
                placeholder=""
                @select="handleSelect"
                icon="search"
                class="top-autocomplete"
                >
            </el-autocomplete>
            <el-button type="primary" @click="openSetting"></el-button>
        </el-button-group>

        </div>
        <!--setting dialog-->
        <el-dialog
            title=""
            :visible.sync="settingVisible"
            size="tiny"
            custom-class="setting-dialog map-dialog slideInDown"
            :modal=false
            @open="onOpenSetting"
            :before-close="handleClose">
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <!-- <el-tab-pane label="" name="first"></el-tab-pane> -->
                <el-tab-pane label="" name="userSetting">
                    <div class="map-setting">
                        <ul>
                            <li style="margin-bottom: 5px;">
                                <label style="width: 80px; display:inline-block;"></label>
                                <el-button type="primary" @click="openUserlist" class="add-user" style="width:150px;"></el-button>
                            </li>
                            <li>
                                <label style="width: 80px; display:inline-block;"></label>
                                <el-button type="primary" @click="logOut" class="add-user" style="width:150px;"></el-button>
                            </li>
                        </ul>

                    </div>
                </el-tab-pane>
                <el-tab-pane label="" name="mapSetting">
                    <div class="map-setting">
                        <label></label>
                        <el-switch
                            v-model="isUpdateTrail"
                            @change="changeUpdateTrail"
                            on-text=""
                            off-text="">
                        </el-switch>
                    </div>

                </el-tab-pane>
            </el-tabs>
            <span slot="footer" class="dialog-footer">
            </span>
        </el-dialog>
        <!--userlist dialog-->
        <el-dialog
            title=""
            :visible.sync="userListVisible"
            size="large"
            custom-class="login-dialog map-dialog slideInDown"
            :modal=true
            @open="onOpenSetting" 
        >
            <div class="userlist" style="overflow: scroll;max-height: 400px;">
                <ul>
                    <li v-for="user of userList"> 
                        <span class="user-avatar boy" v-bind:class="{boy: user.sex == 1, gril: user.sex == 2}"></span>
                        <span class="user-name">{{user.name}}</span>
                        <el-button type="primary" style="float: right" v-if="user.isNotFriend" @click="addUser(user)"></el-button>
                        <el-button type="danger" style="float: right" v-else  @click="unAddUser(user)"></el-button>
                    </li>
                </ul>
            </div>
        </el-dialog>
        <!--login dialog-->
        <el-dialog
            :title=loginTitle
            :visible.sync="loginDialogVisible"
            size="large"
            custom-class="login-dialog map-dialog slideInDown"
            :modal=true
            @open="onOpenSetting"
            :show-close=false
            :before-close="handleLoginClose">
            <login 
                @loginSuccess="loginSuccessCallback"
                v-if="loginComp == 'login'"
                @forgetPass="forgetPassCb"
                @registerAccount="registerAccountCb"
                @userGuide="openUserGuide"
            >
            </login>
            <register
            v-else-if="loginComp == 'register' "
            @registerSuccess="registerSuccessCallback"
            @backToLogin="backToLogin"
            ></register>
            <div class="userGuide" v-else-if="loginComp == 'userGuide'">
                <h3 style="font-size:14px;"> </h3>
                </br>
                    <span class="first-title"> </br></span>
                    <span class="content"> 1. AndroidIOS</br></span>
                    <span class="content"> 2. </br></span>
                    <span class="first-title"> :</span>
                    <span class="content">1. </br></span>
                    <span class="content">2. </span>
                    </br>
                    <span class="content">3. </span>
                    </br>
                    <span class="first-title"> </br></span>
                    <span class="content">1</span>
                    </br>
                    <span class="content">2()</span>
                    </br>
                    <span class="content">320</span>
                    </br>
                    <span class="content">4</span>
                    </br>
                    <span class="content">5()</span>
                    <span class="first-title">  </br></span>
                    
                    <span class="content"></span>
            </div>
            <span slot="footer" class="dialog-footer" v-if="loginComp == 'userGuide'">
                <el-button type="primary" @click="loginComp = 'login'"> </el-button>
            </span>
        </el-dialog>
    </div>

</template>
<script>
import register from './register'
import login from './login'
import Vue from 'vue'
import {mapState} from 'vuex'
import _ from 'lodash'
import utils from '../helper/utils'
import configUrl from './apiConfig'
import { MessageBox } from 'mint-ui';

Vue.component('marker-autocomplete', {
    functional: true,
    render: function (h, ctx) {
        const item = ctx.props.item;
        return h('li', ctx.data, [
            h('div', { 
                attrs: { 
                    class: 'name' 
                } 
            }, [item.name]),
            h('span', { 
                attrs: { 
                    class: 'label' 
                } 
            }, [item.label])
        ]);
    },
    props: {
        item: { 
            type: Object, 
            required: true 
        }
    }
});

export default {
    data() {
        return {
            settingVisible: false,
            loginDialogVisible: false,
            userListVisible: false,
            searchVal: '',
            isUpdateTrail: true,
            activeName: 'userSetting',
            loginComp: 'login',
            loginTitle: '',
            friendList: [],
            userList: [
                {username: 'test1'},
                {username: 'test2'}
            ],
            markerList: []
        }
    },
    components: {
        'login': login,
        'register': register
    },
  methods: {
    handleSelect(selectVal) {
        const me = this
        //find which marker will be hightlight
        const resultMarker =  _.find(this.markerList, (marker) => {
            let extData = marker.getExtData();
            return extData.id == selectVal.id
        })
        me.highLightMarker(resultMarker)
    },
    handleClick() {
        console.log(this.activeName)
    },
    openSetting() {
        this.settingVisible = true
    },
    onOpenSetting() {
        const mapDialog = document.querySelector('.setting-dialog')
        const parentNode = document.querySelector('.top-root > .el-dialog__wrapper')
        utils.createCanvasBg(mapDialog, parentNode, 'imageMask')
    },

    highLightMarker(marker) {
        //get marker object
        //define marker's behavior
        marker.setAnimation('AMAP_ANIMATION_BOUNCE')
        setTimeout(_ => {
            marker.setAnimation('AMAP_ANIMATION_NONE')
        }, 3000)
        console.log(this)
        this.$parent.mapHelper.setMapZoomAndCenter(undefined, marker.getPosition())
    },
    handleClose(done) {
        done()
    },
    querySearchAsync(queryString, cb) {
        const me = this;
        let searchCollection = this.loadSearchCollection();
        let results = queryString ? searchCollection.filter(this.createStateFilter(queryString)) : searchCollection;
        console.log(results)
        clearTimeout(this.timeout);
        this.timeout = setTimeout(_ => {
            cb(results);
        }, 500 * Math.random());
    },
    createStateFilter(queryString) {
        return (state) => {
            return (state.name.indexOf(queryString.toLowerCase()) === 0)
        };
    },
    loadSearchCollection() {
        let extDataArr = []
        _.each(this.markerList, (marker) => {
            let extData = marker.getExtData();
            extDataArr.push({
                name: extData.username,
                id:  extData.id
            })
        })
        return extDataArr
    },
    showLogin() {
        this.loginDialogVisible = true
    },
    loginSuccessCallback() {
        let me = this;
        this.loginDialogVisible = false
        this.$emit('loginSuccess')
        this.getfriends((userList) => {
            me.$emit('onGetUserlist', userList)
        });
        //need refresh the message 
        // let messageRefreshTimer = setInterval(() => {
        //     this.getNewMessage()
        // }, 20000)
    },
    changeFriendStatus(userList) {
        let me = this;
        if (!this.friendList.length) {
            this.friendList = JSON.parse(utils.getLocalStorage('friendList'))
        }
        console.log(userList)
        console.log(this.friendList)
        _.each(userList, (user) => {
            let friend = _.find(me.friendList, {userId: user.userId});
            if (!friend) {
                friend = _.find(me.friendList, {friendId: user.userId});
            }
            if (friend) {
                user.isNotFriend = false;
            } else {
                user.isNotFriend = true;
            }
        })
    },
    registerSuccessCallback() {
        this.loginComp = 'login'
    },
    forgetPassCb() {
        
    },
    registerAccountCb() {
        this.loginTitle = ''
        this.loginComp = 'register'
    },
    handleLoginClose(done) {
        if (utils.getLocalStorage('login')) {
            done()
        }
    },
    openUserlist() {
        this.getUserList((userList) => {
            this.changeFriendStatus(userList)
            this.userListVisible = true
        });
    },
    getUserList(cb) {
        let me = this
        this.$http.get(configUrl.user + '/all')
            .then(res => {
                let result = res.data
                console.log(result)
                if (result.status == 200) {
                    let userList = result.data.userList
                    me.userList = userList
                    _.remove(userList, (user) => {
                        let currentUserId = utils.getLocalStorage('userId')
                        return user.userId == currentUserId
                    })
                    cb && cb(userList)
                    //need refresh the marker
                    //need get the message num
                } else {
                    me.logOut()
                }
            }).catch(err => {
                console.log(err)
            })
    },
    getfriends(cb) {
        let me = this;
        let friendList = [];
        let currentUserId = utils.getLocalStorage('userId')
        this.$http.get(configUrl.user + `/friends/${currentUserId}`)
            .then((res) => {
                console.log(res)
                let result = res.data
                if (result.status == 200) {
                    friendList = result.data.friends
                    utils.setLocalStorage('friendList', JSON.stringify(friendList))
                    me.friendList = friendList
                    cb && cb(friendList)
                } else {
                    MessageBox('', _.result(res, 'data.data.errMsg'));
                    me.logOut()
                }
            })
            .catch(err => {
                console.log(err)
            })
    },

    getNewMessage(cb) {
        //get friend user list
        let me = this
        let friendList = [];
        let currentUserId = utils.getLocalStorage('userId')
        this.$http.get(configUrl.user + `/friends/${currentUserId}`)
            .then((res) => {
                let result = res.data
                if (result.status == 200) {
                    friendList = result.data.friends
                    utils.setLocalStorage('friendList', JSON.stringify(friendList))
                    //get the new message
                    me.friendList = friendList
                    me.$emit('onGetUserlist', friendList)
                    let makeRequest = [];
                    _.each(friendList, (val) => {
                        let toUserId = val.id
                        makeRequest.push(me.$http.get(configUrl.messages + `/1/${currentUserId}/${toUserId}`))
                    })
                    me.$http.all(makeRequest).then(me.$http.spread(function (...results) {
                        _.each(results, function(res) {
                            me.handleNewMsg(res)
                        })
                    }))
                }
            })
            .catch((err) => {
                console.log(err)
            })
        cb && cb()
    },

    handleNewMsg(res) {
        let me = this
        let data = res.data
        let currentUserId = utils.getLocalStorage('userId')
        if (data.status == 200) {
            let msg = JSON.parse(data.msg)
            if (msg.length) {
                _.orderBy(msg, ['cTimestamp']);
                let num = _.filter(msg, (item) => {
                    return item.status == 2 && item.fromId != currentUserId
                })
                let msgNum = num ? num.length : 1
                if (msg[0].status == 2 && msg[0].fromId != currentUserId) {
                    //need refresh the user avatar
                    me.$emit('refreshUserMarker',  msg[0].fromId, msg[0], msgNum)
                }
            }
        }
    },

    addUser(user) {
        let me = this;
        //category = 1 means only see
        //category = 2 means chat and see
        let category = 1;
        let currentUserId = utils.getLocalStorage('userId')
        this.$http.post(configUrl.user + '/addFriend', {
          //category: category,
          fromId: currentUserId,
          toId: user.userId
        })
        .then(res => {
            let result = res.data
            if (result.status == 200) {
                MessageBox('', '');
            } else {
                MessageBox('', result.data.errMsg);
            }
        }).catch(err => {
            console.log(err)
        })
    },

    unAddUser(user) {
        let me = this;
        //category = 1 means only see
        //category = 2 means chat and see
        let category = 1;
        let currentUserId = utils.getLocalStorage('userId')
        this.$http.post(configUrl.user + '/unAddFriend', {
          fromId: currentUserId,
          toId: user.userId
        })
        .then(res => {
            let result = res.data
            if (result.status == 200) {
                MessageBox('', '');
            } else {
                MessageBox('', result.data.errMsg);
            }
        }).catch(err => {
            console.log(err)
        })    
    },

    changeUpdateTrail() {
        if(this.isUpdateTrail) {
            MessageBox('');
        } else {
            MessageBox('');
        }
        this.$emit('changeUpdateTrail', this.isUpdateTrail)
    },

    backToLogin() {
        this.loginComp = 'login'
        this.loginTitle = ''
    },

    openUserGuide() {
        this.loginComp = 'userGuide'
        this.loginTitle = ''
    },

    logOut() {
        utils.clearLocalStorage('token', '');
        utils.clearLocalStorage('userId', '');
        this.isUpdateTrail = true
        this.loginDialogVisible = true
        this.settingVisible = false
    }
  }
}
</script>
<style lang = 'scss'>
body {
    .marker-autocomplete {
        li {
            line-height: normal;
            padding: 7px;

            .name {
            text-overflow: ellipsis;
            overflow: hidden;
            }
            .label {
            font-size: 12px;
            color: #b4b4b4;
            }

            .highlighted .addr {
            color: #ddd;
            }
        }
    }


    .top-con {
        width: 100%;
        position: absolute;
        top: 10px;
        z-index: 200;
        text-align: right;
    }
    .top-autocomplete {
        width: 80%;
        float: left;
    }
    .el-button-group {
        width: 100%;
        padding-left: 20px;
    }

    .userGuide {
        height: 330px;
        overflow: scroll;
        color: #ffffff;
        text-align: left;
        letter-spacing: 1px;
        padding-top: 5px;

        .first-title {
            font-size: 14px;
        }

        .content {
            padding-left: 10px;
            display: inline-block;
        }
    }

    .map-setting {
        width: 100%;
        height: 100%;
        text-align: left;
    }
    .userlist {
        
        li {
            margin: 5px;
            text-align: left;
            padding-bottom: 5px;
            border-bottom: 1px solid #48576a;
        }
        .user-avatar{
            width: 40px;
            height: 40px;
            border-radius: 50%;
            vertical-align: top;
            background: #fff;
            display: inline-block;

            &.boy {
                background: url("../../static/img/boy_avatar.png");
            }

            &.girl {
                background: url("../../static/img/girl_avatar.png");
            }
        }
        .user-name{
            display: inline-block;
            height: 40px;
            line-height: 40px;
            font-size: 20px;
            color: #fff;
            margin-left: 5px;
        }
    }
}

</style>


